.canvas-holder {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: background 1s ease;
}

.ui {
  position: absolute;
  top: 0;
  left: 0;
  padding: 2rem;
  z-index: 2;
  width: 100%;
}

.ui__url {
  width: 100%;
  display: flex;
}
